:root {
  --logo-width : 300px;
  --logo-height : 51px;
  --login-inpu-width : 450px
}

.login{
  margin : 100px auto;
  height: 612px;
  width : 613px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.login-text{
  background-color: white;
  width : var(--login-inpu-width);
  display: flex;
  justify-content: flex-start;
  font-size: 30px;
  font-weight: 550;
  margin-bottom : 10px;
}

.login-input{
  background-color: #ffffff;
  width: var(--login-inpu-width);
  height: 160px;
  display: flex;
  align-items: center;
  justify-content: center;
  direction: row();
  box-shadow: 2px 2px 4px 3px #ea535780;
}

.login-input-group{
  width:330px;
}

.login-input-id, .login-input-pw{
  display: flex;
  height: 30px;
  justify-content: flex-end;
  margin : 15px;
}

.login-input-id-username, .login-input-id-password
{
  width: 220px;
  background-color: #D9D9D9;
  border-style: none;
}
.login-input-id-text, .login-input-pw-text{
  font-weight: 600;
  margin-top: 5px;
  margin-right: 30px;
}
.login-input-pw-text{
  margin-right: 5px;
}

.login-button{
  background-color: #ea535780;
  height : 75px;
  font-weight: 600;
  border-style: none;
}

.logo{
  background-color: #ffffff;
  height: auto;
  width: var(--logo-width);
  margin-bottom : 50px;
}

.mainlogo{
  height: auto;
  margin-top:50px;
}